import { Drawer, Layout, Menu, Breadcrumb, Icon, Row, Col } from "antd";
import Link from "next/link";
import { Input } from "antd";
import { Button } from "antd";
const { TextArea } = Input;
import { Modal } from "antd";
import instance from "../server/chat";
import web3 from "web3";
const { Header, Content, Footer, Sider } = Layout;
const SubMenu = Menu.SubMenu;

// Dummy data for messages
const DUMMY_DATA = [
  {
    senderId: "perborgen",
    text: "who'll win?"
  },
  {
    senderId: "janedoe",
    text: "who'll win?"
  }
];

class Chat extends React.Component {
  constructor(props) {
    super();
    this.state = {
      value: "",
      collapsed: false,
      visible: false,
      messages: DUMMY_DATA,
      modalVisible: false,
      bountySetValue: [],
      ManagerName: "",
      BountyPaid: false,
      MessageArray: []
    };

    this.setModalVisible = this.setModalVisible.bind(this);
    // this.calling = this.calling.bind(this);
  }
   messageSender(event) {
    //event.target.value;
    if (bountyPaid === true) {
      var localAccount = web3.eth.accounts[0];
      console.log(localAccount);
      let msg;
      if (localAccount === manager) {
        var msg = "0" + event.target.value;
      } else {
        var msg = "1" + event.target.value;
      }

     instance.methods.sendMessage(msg).call((err, res) => {
        if (err) console.log("message not sent", err);
        else console.log("message sent successfully", res);
      });
    } else {
      //TODO pay bounty and refresh state
    }
  }
  setModalVisible(modalVisible) {
    this.setState({ modalVisible: true });
  }

  showDrawer = () => {
    this.setState({
      visible: true
    });
  };

  onClose = () => {
    this.setState({
      visible: false
    });
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  async stateSync() {
    await instance.methods.bounty().call((error, response) => {
      if (error) {
        console.log("error", error);
      } else {
        // console.log('response',response);
        this.setState({
          bountySetValue: web3.utils.fromWei(response, "ether")
        });
      }
    });
    await instance.methods.manager().call((error, response) => {
      if (error) {
        console.log("managererror", error);
      } else {
        console.log("managerresponse", response);
        this.setState({
          ManagerName: response
        });
      }
    });

    await instance.methods.bountyPaid().call((error, response) => {
      if (error) {
        console.log("BountyPaiderror", error);
      } else {
        console.log("BountyPaidresponse", response);
        this.setState({
          BountyPaid: response
        });
      }
    });
    await instance.methods.getMessages().call((error, response) => {
      if (error) {
        console.log("GetMessagesPaiderror", error);
      } else {
        console.log("getMessagesResponse", response);
        this.setState({
          MessageArray: response
        });
      }
    });
    console.log("MessageArray", this.state.MessageArray);
  }

  async componentDidMount() {
    //  console.log("2wlehwkufhdkdsahf");
    //  console.log("%%%%%",instance);
    //  console.log(instance.options.address);

    //  bountyPaid

    this.stateSync();

    instance.newMessage().watch((error, response) => {
      if (!error) {
        this.stateSync();
      }
    });
  }

  render() {
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Sider
          collapsible
          collapsed={this.state.collapsed}
          onCollapse={this.onCollapse}
        >
          <div className="logo" />
          <Menu theme="dark" defaultSelectedKeys={["1"]} mode="inline">
            <SubMenu
              key="sub1"
              title={
                <span>
                  <Icon type="user" />
                  <span>User</span>
                </span>
              }
            >
              <Menu.Item key="1">Piyush</Menu.Item>
              <Menu.Item key="2">
                {" "}
                <Link href="/profile">
                  <div>Aditya</div>
                </Link>
              </Menu.Item>
              <Menu.Item key="3">Hemant</Menu.Item>
            </SubMenu>
            <SubMenu
              key="sub2"
              title={
                <span>
                  <Icon type="team" />
                  <span>Team</span>
                </span>
              }
            >
              <Menu.Item key="6">Team Blume</Menu.Item>
              <Menu.Item key="8">Team Hello</Menu.Item>
            </SubMenu>
            <Menu.Item key="9">
              <Icon type="file" />
              <span>File</span>
            </Menu.Item>
            <Menu.Item key="10" onClick={this.showDrawer}>
              <Icon type="setting" theme="outlined" />
              <span>Settings</span>
            </Menu.Item>
            <Menu.Item key="11">
              <Link href="/">
                <div>
                  <Icon type="poweroff" theme="outlined" />
                  <span>Log Out</span>
                </div>
              </Link>
            </Menu.Item>
          </Menu>
        </Sider>
        <Layout>
          <Header style={{ background: "#fff", padding: 0 }} />
          <Content style={{ margin: "0 16px" }}>
            <Breadcrumb style={{ margin: "16px 0" }} />
            <ul className="message-list" />
          </Content>
          <Row>
            <Col xs={{ span: 18 }} lg={{ span: 23 }}>
              <TextArea onChange={this.messageSender} rows={4} />
            </Col>
            <Col xs={{ span: 2 }} lg={{ span: 1 }}>
              <Button type="primary" style={{ height: 95 }}>
                Send
              </Button>
            </Col>
          </Row>
          <Footer style={{ textAlign: "center" }}>
            <b>
              {this.state.MessageArray.forEach(element => {
                <h1>{element}</h1>;
              })}
              Created by Team blume
            </b>
          </Footer>
        </Layout>
        <Drawer
          title="User Settings"
          placement="right"
          closable={true}
          onClose={this.onClose}
          visible={this.state.visible}
        >
          <p type="primary" onClick={() => this.setModalVisible(true)}>
            Set Bounty
          </p>
          <p type="primary">Set Messaging limit</p>
          <p type="primary">Control Privacy</p>
        </Drawer>

        <Modal
          title="Set Bounty"
          style={{ top: 20 }}
          visible={this.state.modalVisible}
          onOk={() => this.setModalVisible(false)}
          onCancel={() => this.setModalVisible(false)}
        >
          Your Current Bounty is {this.state.bountySetValue} Either
        </Modal>
      </Layout>
    );
  }
}

export default Chat;
